p {
  margin: 4px;
}

.App-header {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%;
  z-index: 999;
}

.App-footer {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: rgb(172, 172, 172);
}

.App {
  height: 90vh;
}

.App-container {
  display: flex;
  justify-content: center;
  margin: 50px auto;
  width: 100%;
  height: 100%;
}

.forms {
  display: flex;
  margin: 10px;
  padding: 10px;
  width: 20%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.form-container{
  display: flex;
  flex-direction: column;
  height: 100%;
}

.results {
  display: flex;
  margin: 10px;
  padding: 10px 50px 10px 10px;
  flex-direction: column;
  width: 60%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.output_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}